@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: none; 
  z-index: 1;
  font-family: 'Roboto Condensed', sans-serif;
  line-height: 24px;
}

#root{
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}


#customCoursor{
  position: absolute;
  display: block;
  width: 10px;
  height: 10px;
  border-style: solid;
  border-radius: 5px;
  border-width: 1px;
  border-color: #e3e3e3;
  background-color: #e3e3e3;
  transform: translate(-50%, -50%);
  -webkit-filter: drop-shadow(0 0 1px #e3e3e3);
          filter: drop-shadow(0 0 1px #e3e3e3);
  z-index: 2;
  pointer-events: none;
}

#customCoursorOuter{
  width: 32px;
  height: 32px;
  position: absolute;
  left: 0;
  right: 0;
  border-style: solid;
  border-radius: 16px;
  border-width: 1px;
  border-color: #e3e3e3;
  transform: translate(calc(-50% + 4px), calc(-50% + 4px));
  -webkit-filter: drop-shadow(0 0 1px #e3e3e3);
          filter: drop-shadow(0 0 1px #e3e3e3);
  transition: all ease-in 0.2s;
  z-index: 2;
  pointer-events: none;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(120deg ,#242424, #1c314f); 
