div.sliderButtons{
  position: relative;
  height: 60px;
}

#slideCounter{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  font-size: 20px;
  color: white;
}

div.slideLeft{
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(calc(-1*100% - 20px));
}

div.slideRight{
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(20px);

}


