}

div.inputDescription{
  position: absolute;
  bottom: 2px;
  font-size: 14px;
  color: rgb(150, 150, 150);
  transition: bottom 0.5s;
  pointer-events: none;
}

div.inputUnderline{
  position: absolute;
  height: 24px;
  width: 100%;
  bottom: 0;
  left: -100%;
  pointer-events: none;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-color: white;
  -webkit-filter: drop-shadow(0px 0px 3px white);
          filter: drop-shadow(0px 0px 3px white);
  transition: all 0.5s;
}

input.subjectInput{
  position: absolute;
  bottom: 0;
  left: 0;
  height: 24px;
  width: 100%;
  font-size: 14px;
  background-color: transparent;
  border-style: solid;
  border-width: 0px 0px 1px 0px;
  border-radius: 0px;
  border-color: white;
  color: white;
  outline: none;
}

input.subjectInput:required{
  box-shadow: none;
}

input.subjectInput:focus ~ div.inputDescription{
  bottom: 24px;
  -webkit-filter: drop-shadow(0px 0px 2px rgb(150, 150, 150));
          filter: drop-shadow(0px 0px 2px rgb(150, 150, 150));
}

input.subjectInput:valid ~ div.inputDescription{
  bottom: 24px;
}

input.subjectInput:focus ~ div.inputUnderline{
  left: 0;
}

div.messageContainer{
  position: relative;
}

textarea.messageInput{
  width: 100%;
  height: 200px;
  resize: none;
  background-color: transparent;
