}

div.textareaWrapper{
  position: absolute;
  width: 100%;
  height: 200px;
  top: 0;
  border-style: solid;
  border-width: 1px;
  border-color: white;
  border-radius: 0px;
  pointer-events: none;
}

textarea.messageInput:focus ~ div.textareaWrapper{
  -webkit-filter: drop-shadow(0px 0px 3px white);
          filter: drop-shadow(0px 0px 3px white);
}

